/* eslint-disable @typescript-eslint/no-unused-vars */
/* eslint-disable no-console */
/* eslint-disable no-empty */
/* eslint-disable jsx-a11y/interactive-supports-focus */
import React, { useEffect, useState } from 'react';
import { ethers } from 'ethers';
import * as styles from './mint.module.css';
import NotificationUi from '@/components/Notification';
import { isProductionEnv } from '@/service/tools';
import { CONTRACT_ADDRESS, ABI } from '@/config';

interface TxParamsOverride {
  value: ethers.BigNumber;
  gasLimit?: ethers.BigNumber;
}

type Provider = ethers.providers.InfuraProvider | ethers.providers.Web3Provider;

const Mint: React.FC<unknown> = () => {
  const [mintAmount, setMintAmount] = useState(1);
  const [remainingRoses, setRemainingRoses] = useState(9999);
  const [notification, setNotification] = useState({
    show: false,
    title: ``,
    description: ``,
    isError: false,
  });

  const updateRemainingRosesUi = async () => {
    try {
      const provider = window.ethereum
        ? new ethers.providers.Web3Provider(window.ethereum)
        : new ethers.providers.InfuraProvider(
            isProductionEnv() ? `homestead` : `rinkeby`,
            `e55fcfdbebff4e3e8ca3f75f85770aa3`,
          );
      const contract = new ethers.Contract(CONTRACT_ADDRESS, ABI, provider);
      const totalSupply = Number(await contract.totalSupply());
      setRemainingRoses(9999 - totalSupply);
    } catch (error) {
      console.error(error);
    }
  };

  useEffect(() => {
    // updateRemainingRosesUi();
    setMintAmount(0);
  });

  const handleMetamaskConnection = async (): Promise<boolean> => {
    if (!window.ethereum) {
      setNotification({
        show: true,
        isError: true,
        title: `Oops, Can't See Metamask Installed`,
        description: `You need to have metamask installed to connect, For help visit our Discord`,
      });
      return false;
    }
    try {
      const provider = new ethers.providers.Web3Provider(window.ethereum);
      await provider.send(`eth_requestAccounts`, []);
      return true;
    } catch (error) {
      setNotification({
        isError: true,
        show: true,
        title: `Couldn't Connect with metamask`,
        description: error.message,
      });
      return false;
    }
  };

  const handleCloseNotification = () => {
    setNotification({
      title: ``,
      description: ``,
      show: false,
      isError: false,
    });
  };
  const mintButtonHandler = async () => {
    try {
      if (!(await handleMetamaskConnection())) return;
      const provider = new ethers.providers.Web3Provider(
        window.ethereum,
        `rinkeby`,
      );
      const contract = new ethers.Contract(CONTRACT_ADDRESS, ABI, provider);
      const signer = provider.getSigner();
      const contractSigner = contract.connect(signer);
      const weiValue = mintAmount * 0.058;
      let estimatedGas: ethers.BigNumber;
      try {
        const gasLimit = await contractSigner.estimateGas.purchase(mintAmount, {
          value: ethers.utils.parseEther(weiValue.toString()),
        });
        estimatedGas = gasLimit.mul(BigInt(12)).div(BigInt(10));
        console.debug({ estimatedGas: BigInt(estimatedGas as any).toString() });
      } catch (error) {
        console.error(error);
      }
      const txParamsOverride: TxParamsOverride = {
        value: ethers.utils.parseEther(weiValue.toString()),
      };
      if (estimatedGas) {
        txParamsOverride.gasLimit = estimatedGas;
      }
      const tx = await contractSigner.purchase(mintAmount, txParamsOverride);
      console.debug({ tx });
    } catch (error) {
      console.debug(error.code);
      if (error.code === `INSUFFICIENT_FUNDS`) {
        setNotification({
          isError: true,
          show: true,
          title: `Insufficient ETH balance`,
          description: `Your ETH balance is not enough to pay the mint + gas fees, Please make sure you are using the correct account. For help visit our Discord`,
        });
        return;
      }
      if (error.code === `NETWORK_ERROR`) {
        setNotification({
          isError: true,
          show: true,
          title: `Wrong Network!`,
          description: `It looks that you are not connected to Ethereum Mainnet Network, Please change network to "Ethereum Mainnet" in metamask and refresh the page. For help visit our Discord`,
        });
        return;
      }
      setNotification({
        isError: true,
        show: true,
        title: `Oops, Something Happened!`,
        description: `${error.message}, For help visit our Discord`,
      });
      console.error(error);
    }
  };
  // const handleMintValueChange = async (
  //   e: React.ChangeEvent<HTMLInputElement>,
  // ) => {
  //   const value = Number(e.currentTarget.value);
  //   if (!Number.isNaN(value)) {
  //     setMintAmount(value);
  //   }
  // };
  return (
    <div id="mint" className="mt-28 lg:mt-36 relative">
      <NotificationUi
        isError={notification.isError}
        title={notification.title}
        description={notification.description}
        show={notification.show}
        onClose={handleCloseNotification}
      />
      <h2>Mint</h2>

      <div
        className={`flex flex-col lg:flex-row mt-24 lg:mt-28 items-center space-x-0 justify-around p-4 mx-auto ${styles.glassContainer}`}
      >
        <div className={styles.ethTempleWrapper}>
          <img
            className="relative -top-20 lg:-top-28 w-96"
            src="/images/eth-temple.png"
            alt="Eth"
          />
        </div>
        <div className={`flex flex-col text-center `}>
          <div
            className={`flex flex-col xl:flex-row items-center space-x-0 xl:space-x-20 space-y-3 lg:space-y-0 justify-between ${styles.content}`}
          >
            <span>Roses Remaining</span>
            <h3>{(remainingRoses / 1000).toFixed(3)}/9.999</h3>
          </div>
          <div className={styles.divider} />
          <div
            className={`flex flex-col xl:flex-row items-center space-y-3 xl:space-y-0 justify-between ${styles.content}`}
          >
            <span>Unit Price</span>
            <h3>0.0580 ETH</h3>
          </div>
          <div className={styles.divider} />
          <div
            className={`flex flex-col xl:flex-row items-center space-y-3 xl:space-y-0 justify-between ${styles.content}`}
          >
            <span>Maximum Batch</span>
            <h3>2 per tx</h3>
          </div>
          <div className="flex self-end mt-8 xl:mt-8 space-x-3 lg:space-x-8 items-center">
            <div className="flex flex-col items-center space-y-2">
              <span className="text-gray-400 text-base">Quantity (Max 2)</span>
              <div className="flex space-x-2 items-center">
                <button
                  className={styles.plusMinusButton}
                  onClick={() =>
                    setMintAmount(
                      mintAmount !== 1 ? mintAmount - 1 : mintAmount,
                    )
                  }
                  onKeyDown={() =>
                    setMintAmount(
                      mintAmount !== 1 ? mintAmount - 1 : mintAmount,
                    )
                  }
                  type="button"
                >
                  <span style={{ marginBottom: 4 }}>-</span>
                </button>
                <input
                  className={styles.mintInput}
                  value={0}
                  // onChange={handleMintValueChange}
                  type="text"
                />
                <button
                  type="button"
                  className={styles.plusMinusButton}
                  onClick={() =>
                    setMintAmount(
                      mintAmount !== 2 ? mintAmount + 1 : mintAmount,
                    )
                  }
                  onKeyDown={() =>
                    setMintAmount(
                      mintAmount !== 2 ? mintAmount + 1 : mintAmount,
                    )
                  }
                >
                  <span style={{ marginBottom: 4 }}>+</span>
                </button>
              </div>
            </div>
            <button
              className={`${styles.mintButton} mt-8  ${
                mintAmount === 0 || mintAmount > 2
                  ? `cursor-not-allowed bg-gray-500`
                  : `bg-primary cursor-pointer hover:bg-primary-light`
              } transition-colors duration-500 ease-in-ou`}
              type="submit"
              onClick={() => true}
            >
              Mint
            </button>
          </div>
        </div>
      </div>
      <h2 className="mx-auto mt-40 text-center">Explore the collection?</h2>
      <button
        className={`${styles.mintButton} bg-primary hover:bg-primary-light w-60 xl:w-max mx-auto mt-10 self-end cursor-not-allowed hover:bg-gray-500 transition-colors duration-500 ease-in-ou`}
        type="submit"
      >
        See items on Opensea
      </button>
    </div>
  );
};

export default Mint;
