/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { useEffect, useState } from 'react';
import { ethers } from 'ethers';
import * as styles from './navBar.module.css';
import Notification from '../Notification';
import { isProductionEnv } from '@/service/tools';

// eslint-disable-next-line no-shadow
enum Web3Status {
  'UNKNOWN' = `UNKNOWN`,
  'NOT_CONNECTED' = `NOT_CONNECTED`,
  'NOT_FOUND' = `NOT_FOUND`,
  'CONNECTED' = `CONNECTED`,
}

const NavBar: React.FC<unknown> = () => {
  const [web3Status, setWeb3Status] = useState<Web3Status>(Web3Status.UNKNOWN);
  const [accountAddress, setAccountAddress] = useState(`Connect`);
  const [notification, setNotification] = useState({
    show: false,
    title: ``,
    description: ``,
    isError: false,
  });

  const handleCloseNotification = () => {
    setNotification({
      title: ``,
      description: ``,
      show: false,
      isError: false,
    });
  };

  useEffect(() => {
    window.ethereum?.on(`accountsChanged`, (accounts) => {
      console.debug(accounts, `accountsChanged`);

      if (accounts.length) {
        setWeb3Status(Web3Status.CONNECTED);
        const address = accounts[0];
        setAccountAddress(`${address.slice(0, 6)}...${address.slice(-4)}`);
        return;
      }
      setWeb3Status(Web3Status.NOT_CONNECTED);
      setAccountAddress(`Connect`);
    });
    window.ethereum?.on(`chainChanged`, () => {
      window.location.reload();
    });
  }, []);

  useEffect(() => {
    const metamaskStatus = async (provider: ethers.providers.Web3Provider) => {
      const accounts = await provider.listAccounts();
      if (accounts.length) {
        setWeb3Status(Web3Status.CONNECTED);
        const address = accounts[0];
        setAccountAddress(`${address.slice(0, 6)}...${address.slice(-4)}`);
        return;
      }
      setWeb3Status(Web3Status.NOT_CONNECTED);
      setAccountAddress(`Connect`);
    };
    const { ethereum } = window;
    if (!ethereum) {
      setWeb3Status(Web3Status.NOT_FOUND);
      return;
    }
    /**
     * @see https://docs.metamask.io/guide/ethereum-provider.html#chain-ids
     */
    window.ethereum
      .request({
        method: `wallet_switchEthereumChain`,
        params: [{ chainId: isProductionEnv() ? `0x1` : `0x4` }],
      })
      .catch(console.error);

    metamaskStatus(new ethers.providers.Web3Provider(ethereum));
  }, []);

  const connectHandler = async (
    event: React.MouseEvent<HTMLAnchorElement, MouseEvent>,
  ) => {
    event.preventDefault();
    if (web3Status === Web3Status.NOT_FOUND) {
      setNotification({
        show: true,
        isError: true,
        title: `Oops, Can't See Metamask Installed`,
        description: `You need to have metamask installed to connect, For help visit our Discord`,
      });
      return;
    }
    if (web3Status === Web3Status.CONNECTED) return;
    try {
      const provider = new ethers.providers.Web3Provider(window.ethereum);
      await provider.send(`eth_requestAccounts`, []);
      const address = (await provider.listAccounts())[0];
      setAccountAddress(`${address.slice(0, 6)}...${address.slice(-4)}`);
      setNotification({
        show: true,
        isError: false,
        title: `Welcome To The Club`,
        description: `Please dont mint all the Roses, Leave some for the others ;)`,
      });
    } catch (error) {
      setNotification({
        isError: true,
        show: true,
        title: `Couldn't Connect with metamask`,
        description: error.message,
      });
    }
  };
  return (
    <div
      className={`flex flex-col space-y-5 lg:space-y-0 lg:flex-row justify-between items-center ${styles.root}`}
    >
      <Notification
        isError={notification.isError}
        title={notification.title}
        description={notification.description}
        show={notification.show}
        onClose={handleCloseNotification}
      />
      <div className="flex">
        <svg
          width="32"
          height="54"
          viewBox="0 0 32 54"
          fill="none"
          xmlns="http://www.w3.org/2000/svg"
        >
          <g opacity="0.6">
            <path
              opacity="0.6"
              d="M15.1926 6.52686L9.77344 8.99147L15.1926 12.1931L20.6097 8.99147L15.1926 6.52686Z"
              fill="#010101"
            />
          </g>
          <path
            opacity="0.45"
            d="M9.77344 8.99148L15.1926 12.1931V0L9.77344 8.99148Z"
            fill="#FC00FF"
          />
          <g opacity="0.8">
            <path
              opacity="0.8"
              d="M15.1934 0V12.1931L20.6104 8.99148L15.1934 0Z"
              fill="#FC00FF"
            />
          </g>
          <path
            opacity="0.45"
            d="M0.0166016 31.1848L16.0052 53.7096V40.6308L0.0166016 31.1848Z"
            fill="#FC00FF"
          />
          <g opacity="0.8">
            <path
              opacity="0.8"
              d="M16.0049 40.6308V53.7096L31.9998 31.1848L16.0049 40.6308Z"
              fill="#FC00FF"
            />
          </g>
          <path
            opacity="0.7"
            d="M2.25272 21.5669C2.25272 28.5304 7.81435 34.7001 15.5249 34.7001C17.7787 34.6279 19.5914 32.7714 19.5914 30.499C19.5914 19.1686 11.0064 9.80418 0 8.56946C1.36356 12.6603 2.25272 16.5248 2.25272 21.5669Z"
            fill="#720073"
          />
          <path
            opacity="0.7"
            d="M23.0944 30.499C23.0944 31.135 23.0167 31.7545 22.87 32.3456C26.282 29.967 28.5237 26.0132 28.5237 21.5669C28.5237 16.6327 29.3828 12.7462 30.7775 8.56726C25.8235 9.11413 21.2272 11.3499 17.7275 14.8197C21.2084 19.2846 23.0944 24.7479 23.0944 30.499Z"
            fill="#A22BA3"
          />
        </svg>
        <h6 className="">Buterin Garden</h6>
      </div>
      <div className="flex space-x-8 items-center">
        <a
          className="h-6 w-6 opacity-70"
          target="_blank"
          rel="noreferrer"
          href="https://discord.gg/bxdVRnr7Ch"
        >
          <img height="100%" width="100%" src="svg/discord.svg" alt="Discord" />
        </a>
        <a
          className="h-6 w-6 opacity-70"
          target="_blank"
          rel="noreferrer"
          href="https://twitter.com/ButerinGarden"
        >
          <img height="100%" width="100%" src="svg/twitter.svg" alt="Twitter" />
        </a>
        <a className="h-6 w-6 opacity-70" href="#">
          <img
            height="100%"
            width="100%"
            src="svg/open-sea.svg"
            alt="Open Sea"
          />
        </a>
        <a className="h-6 w-6 opacity-70" href="#">
          <img height="100%" width="100%" src="svg/eth.svg" alt="Etherscan" />
        </a>
        <a className="hidden lg:block" href="#" onClick={connectHandler}>
          <h5
            style={{ borderRadius: 39 }}
            className="hover:bg-primary-light  duration-500 ease-in-out  transition-colors text-sm xl:text-lg py-2 px-10 border border-primary cursor-pointer"
          >
            {accountAddress}
          </h5>
        </a>
      </div>
      <a className="block lg:hidden" href="#" onClick={connectHandler}>
        <h5
          style={{ borderRadius: 39 }}
          className="hover:bg-primary-light text-sm xl:text-lg mt-3 py-2 px-10 border border-primary cursor-pointer"
        >
          {accountAddress}
        </h5>
      </a>
    </div>
  );
};

export default NavBar;
